/* OwnerEditProfile */
@import '../css/index.css';
.editprofile {
  color: white;
  background-color: var(--primary-color);
  opacity: 0.75;
  border-radius: 15px;
  padding: 2%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  font-size: 100%;
  color: white;
}

/* Ownerprofile/editprofile responsive */
@media (max-width: 500px) {
  .editprofile {
    width: 100%;
  }
}
@media (min-width: 750px) {
  .editprofile {
    width: 700px;
  }
}

