/* OwnerProfile */

/* content border */
.profile-content {
  margin-left: auto;
}
/* .edit-link {
  text-decoration: none;
} */

/* modal width */
@media (max-width: 500px) {
  .modal {
    width: 100%;
  }
}

.pending-alert {
  background-color: var(--secondary-color);
  color: var(--primary-color);
  border: none;
}

.live-alert {
  background-color: var(--primary-color);
  color: white;
  border: none;
}

/* OwnerOrderHistory */

.order-table {
  border: 5px solid var(--primary-color);
  border-radius: 10px;
  border-top: 0;
}

/* table header */
.order-list-title {
  background-color: var(--primary-color);
  opacity: 0.9;
  color: white;
}

/* order's table body  */
.table-striped > tbody > tr:nth-child(odd) {
  background-color: white;
}
.table-striped > tbody > tr:nth-child(even) {
  background-color: var(--primary-color-opac);
  color: white;
}

/* view button */
.view-btn {
  background-color: white;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}
.view-btn:hover {
  background-color: var(--primary-color);
  color: white;
}

/* pending button */
.order-status {
  background-color: var(--secondary-color);
  color: white;
  padding: 6px;
  border-radius: 4px;
  border: none;
}

.order-status:hover {
  color: var(--primary-color);
  cursor: pointer;
}

/* modal title */
.modal-header {
  background-color: var(--primary-color-opac);
  color: white;
}

/* modal confirm button */
.modal-confirm-btn {
  background-color: var(--primary-color-opac);
  color: white;
}

/* OwnerMenu */

/* table header */
.table-header-menu {
  background-color: var(--primary-color);
  color: white;
}
/* table border */
.panel-default {
  border: 7px solid var(--primary-color);
  border-radius: 15px;
  border-top: 0;
  width: 700px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 750px) {
  .panel-default,
  .order-table {
    width: 100%;
  }
}
.menu-icon {
  color: var(--secondary-color);
  cursor: pointer;
}
.menu-icon-plus {
  cursor: pointer;
}
.menu-icon:hover,
.menu-icon-plus:hover {
  color: var(--primary-color);
  transition: all 0.5s ease;
}

.new-menu-plus {
  padding-left: 10px;
}

.modal-edit {
  margin-left: auto;
  margin-right: auto;
}

.owner-heading {
  font-size: 195%;
  font-weight: bold;
  color: var(--primary-color);
}

.restaurant-info {
  border: solid 1px;
  color: var(--secondary-color);
  height: 100px;
  width: 300px;
  border-radius: 10px;
  box-shadow: 0 2px 2px rgba(154, 160, 185, 0.05),
    0 2px 2px rgba(166, 173, 201, 0.2);
}

/* close icon in the alert */
.close-live {
  opacity: 1;
}
/* icons for assigning order to drivers */
.assign-driver-icon {
  color: var(--primary-color);
  cursor: pointer;
}
.assign-driver-icon:hover {
  color: var(--secondary-color);
}
